<template>
  <div>
    <button @click="addCircle">Add Circle</button>
  </div>
</template>

<script>
import L from "leaflet";
export default {
  methods: {
    addCircle() {
      L.circle([51.508, -0.11], {
        color: "red",
        fillColor: "#f03",
        fillOpacity: 0.5,
        radius: 500
      }).addTo(this.$parent.$refs['map'].map);
    }
  }
};
</script>